___CSS_LOADER_EXPORT___.push([module.id, `@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: 'Red Hat Text', serif;
}

#map {
  height: 100vh;
}
#ip-title {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  color: hsl(0, 0%, 59%);
  letter-spacing: 2px;
}

#ip-info {
  font-weight: bold;
  color: hsl(0, 0%, 17%);
  font-size: 18px;
}

.leaflet-marker-icon {
  color: hsl(0, 0%, 17%);
}
.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

@media screen and (max-width: 480px) {
  #ip-title {
    font-size: 12px;
  }
  .info-details {
    margin-block: 15px;
  }
  #ip-info-card {
    text-align: center;
  }
  #ip-info {
    font-weight: bold;
    color: hsl(0, 0%, 17%);
    font-size: 18px;
  }
}
